.contacts__container {
  display: grid;
  grid-template-columns: 40.5% auto;
  column-gap: 129px;
  padding-top: 83px;
  padding-bottom: 103px;

  @include vp-1439 {
    grid-template-columns: 42.5% auto;
  }

  @include vp-1199 {
    display: flex;
    flex-direction: column;
    gap: 40px 0;
    padding-top: 88px;
    padding-bottom: 69px;
  }

  @include vp-767 {
    gap: 30px 0;
    padding-top: 67px;
    padding-bottom: 29px;
  }
}

.contacts__description {
  h2 {
    position: relative;
    margin: 0;
    margin-bottom: 23px;
    font-size: 35px;
    line-height: 35px;
    font-weight: 800;
    z-index: 1;

    @include vp-1199 {
      margin-bottom: 18px;
      font-size: 25px;
      line-height: 25px;
    }

    @include vp-767 {
      margin-bottom: 12px;
      font-size: 22px;
      line-height: 22px;
    }

    &::before {
      position: absolute;
      top: -17px;
      left: 30%;
      transform: translateX(-50%);
      font-size: 55px;
      line-height: 55px;
      font-weight: 800;
      color: $color-elephant;
      opacity: 0.03;
      z-index: -1;
      content: attr(data-parent);

      @include vp-1199 {
        top: -13px;
        left: 15.6%;
        font-size: 40px;
        line-height: 40px;
      }

      @include vp-767 {
        top: -4px;
        left: 49.9%;
        width: 100%;
        font-size: 26px;
        line-height: 26px;
      }
    }
  }

  p {
    margin: 0;
    margin-bottom: 49px;
    font-size: 16px;
    line-height: 24px;

    @include vp-1199 {
      margin-bottom: 39px;
      padding-right: 100px;
    }

    @include vp-767 {
      margin-bottom: 29px;
      padding-right: 5px;
      font-size: 14px;
      line-height: 20px;
    }
  }
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 35px;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @include vp-1199 {
    flex-direction: row;
    justify-content: space-between;
    gap: 0 44px;
    padding-right: 1px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 30px 0;
    padding-right: 0;
  }
}

.contacts__item {
  display: grid;
  grid-template-columns: min-content auto;
  gap: 8px 30px;

  @include vp-1199 {
    gap: 6px 10px;
  }

  @include vp-767 {
    row-gap: 8px;
    padding-right: 70px;
  }

  h3 {
    grid-column: 2 / -1;
    grid-row: 1 / 2;
    margin: 0;
    font-size: 18px;
    line-height: 18px;
    font-weight: 600;

    @include vp-1199 {
      font-size: 16px;
      line-height: 16px;
    }

    @include vp-767 {
      font-size: 14px;
      line-height: 14px;
    }
  }

  a,
  address {
    grid-row: 2 / 3;
    font-style: normal;
    font-size: 16px;
    line-height: 24px;

    @include vp-1199 {
      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      line-height: 20px;
    }
  }

  svg {
    grid-column: 1 / 2;
    grid-row: 1 / 3;
    align-self: center;
    width: 26px;
    height: 26px;

    @include vp-1199 {
      align-self: flex-start;
      width: 24px;
      height: 24px;
    }

    @include vp-767 {
      width: 22px;
      height: 22px;
    }
  }
}

.contacts__map {
  margin-top: 75px;

  @include vp-1199 {
    min-height: 340px;
    margin-top: 0;
  }

  @include vp-767 {
    min-height: 320px;
  }
}
