.about {
  position: relative;
  max-width: 1920px;
  min-height: 474px;
  margin: 0 auto;
  margin-bottom: 121px;

  @include vp-1199 {
    min-height: unset;
    margin-bottom: 90px;
  }

  @include vp-767 {
    margin-bottom: 72px;
  }
}

.about__clouds-container {
  position: absolute;
  top: -165px;
  right: 0;
  z-index: -1;

  @include vp-1199 {
    display: none;
  }
}

.about__image-container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  @include vp-1199 {
    top: 7px;
  }

  @include vp-767 {
    position: unset;
    margin-bottom: 47px;
  }

  img {
    width: auto;

    @include vp-767 {
      width: 100%;
    }
  }
}

.about__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;

  @include vp-1439 {
    gap: 115px;
  }

  @include vp-1199 {
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
}

.about__content {
  grid-column: 2 / -1;
  padding-top: 35px;

  @include vp-1199 {
    padding-top: 0;
  }

  h2 {
    position: relative;
    margin: 0;
    margin-bottom: 57px;
    font-size: 35px;
    line-height: 35px;
    font-weight: 800;
    z-index: 1;

    @include vp-1199 {
      margin-bottom: 35px;
      font-size: 25px;
      line-height: 25px;
    }

    @include vp-767 {
      margin-bottom: 33px;
      font-size: 22px;
      line-height: 22px;
    }

    &::before {
      position: absolute;
      top: -17px;
      left: 14.5%;
      transform: translateX(-50%);
      font-size: 55px;
      line-height: 55px;
      font-weight: 800;
      color: $color-elephant;
      opacity: 0.03;
      z-index: -1;
      content: attr(data-parent);

      @include vp-1199 {
        top: -13px;
        left: 18.4%;
        font-size: 40px;
        line-height: 40px;
      }

      @include vp-767 {
        top: -4px;
        left: 49.5%;
        width: 100%;
        font-size: 26px;
        line-height: 26px;
      }
    }
  }

  h3 {
    margin: 0;
    margin-bottom: 15px;
    font-size: 21px;
    line-height: 21px;

    @include vp-1199 {
      font-size: 18px;
      line-height: 18px;
    }

    @include vp-767 {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 16px;
    }
  }

  p {
    margin: 0;

    @include vp-767 {
      line-height: 20px;
    }
  }

  p:first-of-type {
    padding-right: 20px;

    @include vp-1199 {
      padding-right: 0;
    }
  }

  p + h3 {
    margin-top: 31px;

    @include vp-1199 {
      margin-top: 29px;
    }

    @include vp-767 {
      margin-top: 43px;
    }
  }
}
